import React from "react";
import { StyleSheet, Text, View, FlatList } from "react-native";

const HourCell = ({ testText, test2Text }) => (
  <View style={styles.hourCell}>
    <Text>{testText}</Text>
    <View></View>
    <Text>{test2Text}</Text>
  </View>
);

export default function MainSlider({ data }) {
  const objects = data.Ads.Ad;
  console.dir("test", objects);

  const renderItem = ({ item }) => (
    <HourCell testText={item.Id.text} test2Text={item.ManagerName.text} />
  );

  return (
    <View style={styles.container}>
      <FlatList
        nestedScrollEnabled
        horizontal={true}
        data={objects}
        renderItem={renderItem}
        keyExtractor={(item) => item.Id.text}
        showsHorizontalScrollIndicator={true}
        legacyImplementation={false}
        initialScrollIndex={1}
        progressViewOffset={1}
        recordInteraction
      />
      <Text style={styles.text}></Text>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    width: "100%",
    height: 400,
    paddingTop: 10,
    backgroundColor: "red",
    alignItems: "center",
    justifyContent: "center",
  },
  text: {
    justifyContent: "center",
    alignItems: "center",
  },
  hourCell: {
    backgroundColor: "#1D94FF",
    borderRadius: 10,
    width: 600,
    height: 300,
    marginVertical: 8,
    marginHorizontal: 5,
  },
});
